import { Resend } from 'resend';
import { NextResponse } from 'next/server';

export async function POST(request: Request) {
    try {
        const { body } = await request.json();
        const apiKey = process.env.RESEND_API_KEY;

        if (!apiKey) {
            return NextResponse.json({ error: 'API Key Missing', details: 'RESEND_API_KEY가 .env.local에 설정되지 않았습니다.' }, { status: 500 });
        }

        const resend = new Resend(apiKey);

        const { data, error } = await resend.emails.send({
            from: 'onboarding@resend.dev',
            to: 'xmforxm98@gmail.com',
            subject: 'New Feedback from Loop',
            text: body,
        });

        if (error) {
            console.error('Resend API Error:', error);
            return NextResponse.json({ error: 'Resend API Error', details: error }, { status: 500 });
        }

        return NextResponse.json({ success: true, data });
    } catch (error) {
        console.error('Feedback API Error:', error);
        return NextResponse.json({
            error: 'Failed to send feedback',
            details: error instanceof Error ? error.message : JSON.stringify(error)
        }, { status: 500 });
    }
}
